// post请求
import {
	getMethod
} from '../../api/api.js'
// 请求地址
import {
	getLogin
} from '../../api/request.js'
//引入类
let logins = require('../../login/login.js')
export default {
	data() {
		return {
			wxlogin: false,
			usering: {}
		}
	},

	methods: {
		// // 获取头像昵称
		getUserInfo(event) {
			let wxing = event.detail.userInfo
			console.log(wxing)
			// 实例化类
			const logclass = new logins(wxing)
			// 实例化类之后。可以访问类中的方法和属性
			logclass.listing()
				.then((res) => {
					this.ifUser()
				})
				.catch((err) => {})

		},

		// 是否已登录
		ifUser() {
			let setdata = uni.getStorageSync('usermen')
			if (!setdata) {
				this.wxlogin = false
			} else {
				this.wxlogin = true
				this.usering = setdata
			}
		}

	},

	onShow() {
		this.ifUser()
	}
}
